import React from "react";
import { css } from "@emotion/core";
import { Link } from "gatsby";
import { rhythm } from "../utils/typography";
import { MDXProvider } from "@mdx-js/react";
import SEO from "../components/seo"

import "./HeaderStyles.css";

// require(`katex/dist/katex.min.css`)

const MyH1 = (props) => (
  <header>
    <section className='hero-header-text'>
      <h2 style={{ color: "white", margin: ".5rem" }}>{props.children}</h2>
    </section>
  </header>
);

const MyH2 = (props) =>
  <div style={{ backgroundColor: "#4CAF50", borderRadius: "1rem", marginTop: "2rem" }}>
    <h2 style={{ color: "#E7F4E7", padding: ".8rem" }}>{props.children}</h2>

  </div>

const MyH3 = (props) => <h3  {...props} style={{ color: "#4CAF50", margin: ".5rem" }}>{props.children}</h3>;

const MyParagraph = (props) => <p {...props} >{props.children}</p>;

const components = {
  h1: MyH1,
  h2: MyH2,
  h3: MyH3,
  p: MyParagraph
};

export default (props) => (<MDXProvider components={components}>
  <SEO title={props.pageContext.frontmatter.title} />
  <div
    css={css`
  margin: 0 auto;
  max-width: 700px;
  padding: ${rhythm(2)};
  padding-top: ${rhythm(1.5)};
`}
  >
    <Link to={`/`}>
      <h3
        css={css`
      margin-bottom: ${rhythm(2)};
      display: inline-block;
      font-style: normal;
      color: darkgreen;
    `}
      >
        Econolabs
  </h3>
    </Link>
    {props.children}
  </div>
</MDXProvider>)
  ;



